@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  margin: 0;
  padding: 0;
  cursor: pointer;
}

body {
  min-width: 1600px;
  background: linear-gradient(to right, #1aa363e0, #b946fc);
}

#digital {
  padding-top: 30px;
}

.container {
  border: 1px solid rgb(62, 100, 195);
  text-align: center;
  color: aqua;
  margin: 40px auto;
  position: relative;
  width: 35%;
  height: 250px;
  border-radius: 100px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, .37);
  text-shadow: 2px 2px 4px rgba(255, 255, 255, 0.2);
  border-left: 1px solid rgba(255, 255, 255, 3);
  border-top: 1px solid rgba(255, 25, 255, 3);
}


.flex {

  display: flex;
  justify-content: center;
  padding: 85px;
  
  justify-content: space-around;
  font-size: 45px;
  text-align: center;



}

.child {
  border: 1px solid rgb(62, 100, 195);
  padding: 7px;
  border-radius: 13px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, .37);
  border-left: 1px solid rgba(255, 255, 255, 3);
  border-top: 1px solid rgba(255, 25, 255, 3);
  text-shadow: 2px 2px 4px rgba(255, 255, 255, 0.2);
  padding-bottom: 10px;
  position: relative;
  bottom: 80px
}

.hrtag {
  display: flex;
  padding-right: 68px;
  justify-content: center;
  font-size: 15px;


}

.float {
  padding: 10px;
  padding-inline-start: 75px;
  text-shadow: 2px 2px 4px rgba(255, 255, 255, 0.2)
}